import { Component, h, Prop, Host, State, Element, Watch } from '@stencil/core';

@Component({
	tag: 'o-textfield',
	styleUrl: 'o-textfield.scss',
	shadow: true,
	formAssociated: true
})
export class OTextfield {
	private defaultValue: string = '';
	private valueHasChanged: boolean = false;

	/** @prop {string} nameValue - The name attribute for the input. Default is 'default'. */
	@Prop() nameValue: string = 'default';

	/** Label for the text field */
	@Prop() label: string = '';

	/** Placeholder text for the input */
	@Prop() placeholder: string = '';

	/** Input type (e.g., text, password, email) */
	@Prop() type: 'text' | 'password' | 'email' | 'number' | 'time' = 'text';

	/** Indicates if the input is required */
	@Prop() required: boolean = false;

	/** Indicates if the input is disabled */
	@Prop() disabled: boolean = false;

	/** @prop {string} errorText - The error message to display when validation fails. */
	@Prop() errorText: string = '';

	/** Help text to display below the input */
	@Prop() helpText: string = '';

	/** Value of the input field */
	@Prop({ mutable: true, reflect: true }) value?: string = '';

	/** Indicates if the input has an error */
	@State() hasError: boolean = false;

	/** Reference to the host element */
	@Element() el: HTMLElement;

	private internals: ElementInternals | null = null;

	constructor() {
		// Validation to allow unit tests to run without ElementInternals support
		try {
			if (typeof this.el.attachInternals === 'function') {
				this.internals = this.el.attachInternals();
			}
		} catch (e) {
			/* istanbul ignore next */
			console.warn('ElementInternals not supported', e.message);
		}
	}

	connectedCallback() {
		this.validateInput(this.value);
		/* istanbul ignore if */
		if (this.internals?.setFormValue) {
			try {
				this.internals.setFormValue(this.value);
			} catch (e) {
				console.warn('Form value update not supported', e.message);
			}
		}
	}

	componentDidLoad() {
		this.defaultValue = this.value;
	}

	@Watch('value')
	valueChanged(newValue: string) {
		if (!this.valueHasChanged && this.defaultValue === newValue) {
			return;
		}
		this.validateInput(newValue);
		/* istanbul ignore if */
		if (this.internals?.setFormValue) {
			try {
				this.internals.setFormValue(newValue);
			} catch (e) {
				console.warn('Form value update not supported', e.message);
			}
		}
		this.valueHasChanged = true;
	}

	@Watch('errorText')
	errorTextChanged() {
		/* istanbul ignore next */
		this.validateInput(this.value);
		/* istanbul ignore next */
		this.updateValidityState();
	}

	@Watch('helpText')
	helpTextChanged() {
		this.validateInput(this.value);
	}

	/** Validates the input value */
	validateInput(_value: string) {
		this.hasError = this.errorText.trim() !== '';
		this.updateValidityState();
	}

	/** Update validity state for form association */
	private updateValidityState() {
		if (!this.internals) return;
		// Allow unit tests to run without ElementInternals support
		try {
			/* istanbul ignore if */
			if (this.hasError) {
				this.internals.setValidity?.({ customError: true }, this.errorText, this.el.shadowRoot.querySelector('input'));
			} else {
				this.internals.setValidity?.({});
			}
		} catch (e) {
			/* istanbul ignore next */
			console.warn('Validity state update not supported', e.message);
		}
	}

	/** Handle input change */
	handleInput(event: Event) {
		const input = event.target as HTMLInputElement;
		this.value = input.value;
		this.validateInput(this.value);
		this.internals.setFormValue(this.value);
	}

	render() {
		const baseClasses =
			'placeholder:text-content-subtle text-content-default border-width-sm rounded-radius-lg py-2 px-3 w-full body-default appearance-none focus:outline-width-lg focus:-outline-offset-2 focus-visible:outline-width-lg focus:outline-focus-ring-color-border-default focus-visible:ring-focus-ring-color-border-default';
		const disabledClasses = this.disabled
			? `${baseClasses} border-form-color-border-disabled text-content-disabled bg-form-color-background-disabled cursor-not-allowed`
			: '';

		const errorClasses = this.hasError ? `${baseClasses} border-border-status-error` : '';

		const defaultClasses =
			!this.hasError && !this.disabled
				? `${baseClasses} border-form-color-border-default bg-form-color-background-default`
				: '';

		// Check if startIcon and endIcon slots are filled
		const hasStartIcon = !!this.el.querySelector('[slot="startIcon"]');
		const hasEndIcon = !!this.el.querySelector('[slot="endIcon"]');

		// Add padding if startIcon or endIcon slots are filled
		const iconPaddingClasses = `
			${hasStartIcon ? 'pl-10' : ''}
			${hasEndIcon ? 'pr-10' : ''}
		`;

		return (
			<Host>
				<div class="antialiased relative">
					{this.label && (
						<o-label
							htmlFor={this.nameValue}
							label={this.label}
							variant={this.hasError ? 'error' : this.disabled ? 'disabled' : undefined}
							required={this.required}
						></o-label>
					)}
					<div class="relative">
						{/* Conditionally render Start Icon Slot */}
						{hasStartIcon && (
							<div class="absolute inset-y-0 left-3 flex items-center pointer-events-none">
								<slot name="startIcon"></slot>
							</div>
						)}
						<input
							class={`${defaultClasses} ${errorClasses} ${disabledClasses} ${iconPaddingClasses}`}
							id={this.nameValue}
							type={this.type}
							placeholder={this.placeholder}
							value={this.value}
							disabled={this.disabled}
							required={this.required}
							onInput={(event: Event) => this.handleInput(event)}
							{...(this.disabled ? { disabled: true } : {})}
						/>
						{/* Conditionally render End Icon Slot */}
						{hasEndIcon && (
							<div class="absolute inset-y-0 right-3 flex items-center pointer-events-none">
								<slot name="endIcon"></slot>
							</div>
						)}
					</div>
					{this.hasError && (
						<div class="body-default mt-1">
							<span class="text-content-status-error">{this.errorText}</span>
						</div>
					)}
					{this.helpText && !this.hasError && (
						<div class={`body-default mt-1 ${this.disabled ? 'text-content-disabled' : 'text-content-default'}`}>
							{this.helpText}
						</div>
					)}
				</div>
			</Host>
		);
	}
}
